<template>
  <div>
    <div class="count">{{ h_count }}</div>
    <component
      v-for="(item, key) in obj"
      :key="key"
      :ref="key"
      :is="item.component"
      @end_anim="deleteItem"
      :props_data="item.emoji"
    >
    </component>
    <v-row class="grid">
      <v-col v-for="n in 9" :key="n" class="d-flex child-flex" cols="4">
        <v-img
          class="shadow"
          @click="addHeart(n)"
          aspect-ratio="1"
        >
          <div class="font">{{ getEmoji(n) }}</div>
        </v-img>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import EmojiIcon from '@/components/EmojiIcon.vue';

export default {
  data() {
    return {
      h_count: 0,
      imgs: [],
      timer: null,
      dic_heart: {},
      obj: {},
    };
  },
  components: {
    EmojiIcon,
  },
  methods: {
    addHeart(index) {
      this.obj["idx" + this.h_count] = {
        component: EmojiIcon,
        emoji: this.getEmoji(index)
      };
      this.h_count++;
    },
    deleteItem(payload) {
      this.$delete(this.obj, payload);
    },
    getEmoji(index) {
      switch (index) {
        case 0: return "😍"
        case 1: return "🥳"
        case 2: return "✌🏻"
        case 3: return "😺"
        case 4: return "🤡"
        case 5: return "👍🏻"
        case 6: return "💋"
        case 7: return "⭐️"
        case 8: return "🌹"
        default: return "😍"
      }
    }
  },
};
</script>

<style scoped>
.grid {
  padding: 50px;
}

.count {
  opacity: 0;
}

.font {
  user-select: none;
  /* background-color: black; */
  font-size: 100px;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.shadow {
  border-radius: 20px;
  box-shadow: 1px 1px 4px 1px lightgrey;
}
</style>
