<script>
import $ from 'jquery'

export default {
	data() {
		return {
			installer: undefined
		};
	},
	created() {
		window.addEventListener('beforeinstallprompt ', function(e) {
			e.preventDefault();
			console.log(e)

			window.promptEvent = e;
		})

		this.installer = () => {
			window.promptEvent.prompt();

			window.promptEvent.userChoice.then(function(choiceResult) {
				if (choiceResult.outcome === 'accepted') {
					console.log('User accepted')
				} else {
					console.log('User denined')
				}

				window.promptEvent = null
			})
		}

	},
    mounted() {
        var ggangPano = function(){
		var panowrap = $(".sj_panorama .pano_wrap");
		var	listWrap = panowrap.children();
		var	item = listWrap.children();
		var	listClone = null;
		var	itemWidth = item.width();
		var	itemLength = item.length;
		var	listWidth = itemWidth * itemLength;
		var	move = 0;
		var	timer;

        function _init(){
            panowrap.css({width: (listWidth) * 2 + "px"})
            listWrap.css({width: listWidth + "px"});
            listWrap.clone().appendTo(panowrap);
            listClone = panowrap.children();

            _render();
        }

        function _render(){
            if( listWidth <= move){

                listClone.css({"transform":"translateX(" + 0  + "px)" });
                move = 0;
            }

            move +=1;
            listClone.css({"transform":"translateX(" + -move  + "px)" });
            timer = window.requestAnimationFrame(_render);
        }
        
        window.requestAnimFrame = (function() {
            return window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || window.oRequestAnimationFrame || window.msRequestAnimationFrame ||function (callback) {window.setTimeout(callback, 1000);}
        });

        console.log(timer);
        _init();
	};

	ggangPano();
    }

}
</script>

