<template>
  <v-app id="inspire">
    <v-navigation-drawer
      v-model="drawer"
      :clipped="$vuetify.breakpoint.lgAndUp"
      app
      temporary
    >
    
      <v-list dense>
        <v-list-item two-line class="pl-3">
          <v-avatar color="primary" class="mr-5">
            <span class="white--text headline">SY</span>
          </v-avatar>

          <v-list-item-content>
            <v-list-item-title>SuYoung Kim</v-list-item-title>
            <v-list-item-subtitle>Developer</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-divider class="mt-3"></v-divider>
        
        <div v-for="item in items" :key="item.index">
          <v-list-item
            :key="item.text"
            :to="item.path"
            link
          >
            <v-list-item-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>
                {{ item.text }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <div v-if="item.divider">
            <v-divider></v-divider>
          </div>
        </div>
      </v-list>

      <template v-slot:append>
        <div class="pa-5">
          <v-switch
          @click="setDarkMode" v-model="darkStatus" class="mode_switch" inset label="다크모드 설정"></v-switch>
        </div>
      </template>
    </v-navigation-drawer>

    <v-app-bar
      :clipped-left="$vuetify.breakpoint.lgAndUp"
      app
      color="white darken-3"
      dark
    >
      <v-app-bar-nav-icon color="black" @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title
        style="width: 300px; color: black;"
        class="ml-0 pl-4"
      >
        <v-toolbar-title>SKIM</v-toolbar-title>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn text target="_blank" color="black" href="https://dev-skim.blogspot.com/">
        Blog
      </v-btn>
      <v-btn text target="_blank" color="black" href="https://github.com/SwimingKim">
        GitHub
      </v-btn>
      <!-- <v-btn icon>
        <v-icon>mdi-github</v-icon>
      </v-btn>
      <v-btn icon>
        <v-icon>mdi-web</v-icon>
      </v-btn> -->
    </v-app-bar>
    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
  export default {
    props: {
      source: String,
    },
    data: () => ({
      drawer: null,
      darkStatus: false,
      items: [
        { icon: 'mdi-home', text: 'Main', path: '/main' },
        { icon: 'mdi-apps', text: 'Project', path: '/project', divider: true },
        { icon: 'mdi-emoticon-outline', text: 'Emoji', path: '/emoji' },
        { icon: 'mdi-calendar-check', text: 'History', path: '/history' },
      ],
    }),
    created() {
      const isDark = localStorage.getItem('SKIM_DARK_MODE')
      this.darkStatus = isDark === 'true'
      if (this.darkStatus === true) {
        this.$vuetify.theme.dark = true;
      } else {
        this.$vuetify.theme.dark = false;
      }
    },
    methods: {
      setDarkMode() {
        this.$vuetify.theme.dark = this.darkStatus
        localStorage.setItem('SKIM_DARK_MODE', this.darkStatus)
      }
    }
  }
</script>

<style scoped>

.mode_switch {
  position: relative;
  left: 20px;
}

</style>